@import 'system/functions';

/*------------------------------------*\
  $VARIABLES
\*------------------------------------*/
/**
 * @name Variables
 * @level 2.
 *
 * CONTENTS
 * COLOR-SHEME.........Set theme colors
 * TYPOGRAPHY..........
 * BREAKPOINTS.........
 * SPACING.............
 * LAYOUT..............
 * COMPONENTS..........
 * SCAFFOLDING.........
 */



/*------------------------------------*\
  $COLOR-SHEME
\*------------------------------------*/
/**
 * Basic colors
 * Fancy generator: http://coolors.co/
 */
$cs-a: #16193B;
$cs-a-contrast: #fff;

$cs-b: #35478C;
$cs-b-contrast: #fff;

$cs-c: #4E7AC7;
$cs-c-contrast: #fff;

$cs-d: #7FB2F0;
$cs-d-contrast: #fff;

$cs-e: #ADD5F7;
$cs-e-contrast: #fff;

/**
 * Colors for alerts
 * e.g. success, error, info,...
 */
$cs-positive: #468847;
$cs-neutral:  #3A87AD;
$cs-cautious: #c09853;
$cs-negative: #b94a48;

/**
 * Grey
 */
$cs-grey-a: #efefef;
$cs-grey-b: darken($cs-grey-a, 10%);
$cs-grey-c: darken($cs-grey-b, 10%);



/*------------------------------------*\
  $TYPOGRAPHY
\*------------------------------------*/
/**
 * Set the font weight variables according to the font
 * e.g. http://fonts.googleapis.com/css?family=Source+Sans+Pro:200,400,700
 * In this case set the font weights to 200, 400 and 700
 *
 * If you set a font weight in your css you must use this variables!
 * e.g. font-weight: $font-a-weight-normal;
 *
 * 1. must be in px, do not use in your styles
 */
$font-base-size:        16px; /* [1] */
$font-base-line-height: 24px; /* [1] */
$font-base-color:       #222;

/**
 * Font-sizes
 */
$font-size-xs:   0.75em;
$font-size-s:    0.85em;
$font-size-m:    1em;
$font-size-l:    1.25em;
$font-size-xl:   1.5em;
$font-size-xxl:  1.875em;
$font-size-xxxl: 2.25em;

$font-size-h1: $font-size-xxxl;
$font-size-h2: $font-size-xxl;
$font-size-h3: $font-size-xl;
$font-size-h4: $font-size-l;
$font-size-h5: $font-size-m;
$font-size-h6: $font-size-m;

/**
 * Font A
 */
$font-a-family: 'Helvetica Neue', sans-serif;

$font-a-weight-thin:        100;
$font-a-weight-extra-light: 200;
$font-a-weight-light:       300;
$font-a-weight-normal:      400;
$font-a-weight-medium:      500;
$font-a-weight-semi-bold:   600;
$font-a-weight-bold:        700;
$font-a-weight-extra-bold:  800;
$font-a-weight-ultra-bold:  900;

/**
 * Font B
 */
$font-b-family: Futura, 'Trebuchet MS', sans-serif;

$font-b-weight-thin:        100;
$font-b-weight-extra-light: 200;
$font-b-weight-light:       300;
$font-b-weight-normal:      400;
$font-b-weight-medium:      500;
$font-b-weight-semi-bold:   600;
$font-b-weight-bold:        700;
$font-b-weight-extra-bold:  800;
$font-b-weight-ultra-bold:  900;

/**
 * Anchors
 */
$a-color:                 $cs-a;
$a-color-visited:         $a-color;
$a-color-hover:           $a-color;
$a-text-decoration:       underline;
$a-text-decoration-hover: none;

/**
 * Text selection
 */
$selection-background: $cs-a;
$selection-color:      $cs-a-contrast;



/*------------------------------------*\
  $BREAKPOINTS
\*------------------------------------*/

$breakpoint-s:  30em;
$breakpoint-m:  45em;
$breakpoint-l:  60em;
$breakpoint-xl: 75em;

$breakpoint-max-s:  avalanche-breakpointmax($breakpoint-s);
$breakpoint-max-m:  avalanche-breakpointmax($breakpoint-m);
$breakpoint-max-l:  avalanche-breakpointmax($breakpoint-l);
$breakpoint-max-xl: avalanche-breakpointmax($breakpoint-xl);



/*------------------------------------*\
  $SPACING
\*------------------------------------*/

$spacing-xs:   0.5em;
$spacing-s:    1em;
$spacing-m:    1.25em;
$spacing-l:    2em;
$spacing-xl:   3em;
$spacing-xxl:  4em;
$spacing-xxxl: 5em;



/*------------------------------------*\
  $LAYOUT
\*------------------------------------*/

$layout-max-width: 62em;
$layout-box-sizing-reset: true;



/*------------------------------------*\
  $COMPONENTS
\*------------------------------------*/
/**
 * @name Components
 * @level 3.
 */

/**
 * Grid
 */
$cmp-grid: true;

$cmp-grid-item-count: 12;

// Sizes
$cmp-grid-size-s:  true;
$cmp-grid-size-m:  true;
$cmp-grid-size-l:  true;
$cmp-grid-size-xl: true;

// Spacing between items
$cmp-grid-spacing-s:  $spacing-s;
$cmp-grid-spacing-m:  $spacing-m;
$cmp-grid-spacing-l:  $spacing-m;
$cmp-grid-spacing-xl: $spacing-m;

/**
 * Button
 */
$cmp-button: true;

// Sizes
$cmp-button-size-s:    true;
$cmp-button-size-l:    true;
$cmp-button-size-xl:   true;
$cmp-button-size-full: true;

// Font sizes
$cmp-button-font-size-s:  true;
$cmp-button-font-size-l:  true;
$cmp-button-font-size-xl: true;

// Functions
$cmp-button-function-positive: true;
$cmp-button-function-neutral:  true;
$cmp-button-function-cautious: true;
$cmp-button-function-negative: true;
$cmp-button-function-inactive: true;
$cmp-button-function-disabled: true;

/**
 * Menu
 */
$cmp-menu: true;

// Breakpoint for mobile version of menu
$cmp-menu-mobile-breakpoint: $breakpoint-m;

// Off canvas
$cmp-menu-off-canvas: true;

// Horizontal
$cmp-menu-horizontal: true;
$cmp-menu-horizontal-responsive: true;

// Menu vertical
$cmp-menu-vertical: true;

/**
 * Island
 */
$cmp-island: true;

// Functions
$cmp-island-function-positive: true;
$cmp-island-function-neutral:  true;
$cmp-island-function-cautious: true;
$cmp-island-function-negative: true;

// Color Sheme
$cmp-island-cs-a: true;
$cmp-island-cs-b: true;
$cmp-island-cs-c: true;
$cmp-island-cs-d: true;
$cmp-island-cs-e: true;

/**
 * Media
 */
$cmp-media: true;



/*------------------------------------*\
  $SCAFFOLDING
\*------------------------------------*/

@import 'system/scaffolding';